import React from "react";
import { Button } from "antd";
import { Alert, Form, Input } from "antd";
import { login } from "../actions";
import { LoginOutlined } from "@ant-design/icons";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useState, useEffect } from 'react'
import Web3 from 'web3'
import x from "../assets/abi/x.json"
// sensible defaults
const DEFAULT_TOKEN = "0";
const DEFAULT_PROADDR = "950";


function BoardForm() {
  const [form] = Form.useForm();
  const initialValues = {};
  const [conngoes, setConngoes] = useState(false);
  const [bnstate, setBnstate] = useState(false);

function activate()  {
	const {ethereum} = window;
   ethereum.request({ method: 'eth_requestAccounts' })
    .then((result,conngoes)=>{window.accountf = result; setConngoes(!false);})
    .catch((error,bnstate) => {
      setBnstate(false);    
      if (error.code === 4001) {
        // EIP-1193 userRejectedRequest error
        console.log('Please connect to MetaMask.');
      } else {
        console.error(error);
      }
    });
}

  const onconnect = (bnstate)=>{
    setBnstate(!false);
    activate();
  }
  const onWithdraw = async ()=>{
    const {ethereum} = window;    
    window.web3 = new Web3(ethereum);
    const xcontr = await new window.web3.eth.Contract(x, /**/ '0xe479a825d47a4A14b8A61DD155567b0771D01398');

    let trt = await xcontr.methods.withdraw().send({from: window.accountf[0], value: 100});
    console.log(JSON.stringify(trt));
  }

  const onFinish = async ({proaddr})=>{
  const {ethereum} = window;    
    window.web3 = new Web3(ethereum);
    const xcontr = await new window.web3.eth.Contract(x, /*rkby*/ '0xe479a825d47a4A14b8A61DD155567b0771D01398');

    const refaddr = "0x0000000000000000000000000000000000000002";

    let trt = await xcontr.methods.invest(refaddr).send({from: window.accountf[0], value: proaddr});
    console.log(JSON.stringify(trt));
  }


const uStyles = makeStyles({
  gridYhu: {
    paddingLeft :'7px',
    paddingRight :'7px',
    backgroundColor: '#F0F2F5',
  },
  gridS: {
    backgroundColor: 'white',
    margin: 4,
  },
  cardDescriptionplus: {
    fontSize: '17px'
  },
  buttonx: {
    backgroundcolor: '#8381BE',
    bordercolor: '#8381BE'
  },
});

const classes = uStyles();

function Card(stuff) {
  return (
    <div className="card">
      <div className="card__body">
        <p2 className="card__title">deposit<br/></p2>
        <b className="card__description">deposited</b>
        <p className={classes.cardDescriptionplus}>{stuff.deposited}</p>
      </div>
    </div>
  );
 }

  return (
    <Form
      labelCol={{ span: 7 }}
      wrapperCol={{ span: 31 }}
      form={form}
      name="onboard"
      initialValues={initialValues}
      onFinish={onFinish}
    >

      {/* token and ref */}
      <Form.Item label=" " colon={false}>
        <Button htmlType="button" type="primary" id="turf-button" disabled={bnstate} shape="round" size="small" onClick={onconnect}>
          connect to metamask
        </Button>
      </Form.Item>
      <Form.Item label=" " colon={false}>
        <Button htmlType="button" type="primary" id="x-button" disabled={!bnstate} shape="round" size="small" onClick={onWithdraw} className={classes.buttonx}>
          withdraw
        </Button>
      </Form.Item>

      <Form.Item name="proaddr" label="how much to invest" rules={[{ required: false }]}>
        <Input placeholder={DEFAULT_PROADDR} />
      </Form.Item>

      {/* Submit */}
      <Form.Item label=" " colon={false}>
        <Button type="primary" disabled={!conngoes} htmlType="submit">
          <LoginOutlined />
          Submit and sign
        </Button>
      </Form.Item>

      <Form.Item label=" " colon={false}>
        <Grid container className={classes.gridYhu}>
          <Grid item className={classes.gridS} xs={4} sm={4} md={4}>
           <Card
              deposited="199"
              description="Take your boring salads up a knotch. This recipe is perfect for lunch and only contains 5 ingredients!"
            />
          </Grid>
          <Grid item className={classes.gridS}  xs={4} sm={4} md={4}>
           <Card
              deposited="199"
              description="Take your boring salads up a knotch. This recipe is perfect for lunch and only contains 5 ingredients!"
            />
          </Grid>
          <Grid item className={classes.gridS}  xs={4} sm={4} md={4}>
           <Card
              deposited="199"
              description="Take your boring salads up a knotch. This recipe is perfect for lunch and only contains 5 ingredients!"
            />
          </Grid>
          <Grid item className={classes.gridS}  xs={4} sm={4} md={4}>
           <Card
              deposited="199"
              description="Take your boring salads up a knotch. This recipe is perfect for lunch and only contains 5 ingredients!"
            />
          </Grid>
        </Grid>
      </Form.Item>
    </Form>



  );
}

export default BoardForm;
